  background: rgba(0, 0, 0, 0.8);
  content: '';
  opacity: 0;
  transition: opacity 0.1s linear;
  z-index: 2;
}
.team_member:hover:before {
  opacity: 1;
}
.team_member:hover .team_member_content_container {
  opacity: 1;
}
.team_member_content_container {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-end;
  height: 100%;
  opacity: 0;
  padding: 30px 25px;
  transition: opacity 0.15s linear;
  z-index: 5;
}
.team_member_title {
  color: #fff;
  font-size: 42px;
  margin: 0;
  line-height: 0.9;
}
.team_member_subtitle {
  margin: 5px 0 0 0;
}
.team_member_content {
  color: #fff;
  font-size: 16px;
  margin: 20px 0 0 0;
  line-height: 1.4;
}
